html {
  scroll-behavior: smooth;
  background-color: #000000;
}
ul {
  padding: 0;
  margin: 0;
  list-style-position: inside;
  list-style-type: circle;
}
li {
  padding: 0px;
}

.App {
  text-align: center;
}

.AppBox {
  min-height: 100vh;
  user-select: none;
}
.AppBoxInner {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  gap: 0;
}
.AppBoxToolbar {
  flex-grow: 0;
}
.AppBoxContainer {
  flex-grow: 1;
}

.Navbar {
  background: 'transparent;';
}
.About {
  min-height: 100vh;
}
.Info {
  min-height: 100vh;
}
.timestart {
  margin-bottom: 4px;
  height: 4px;
  width: 4px;
  margin-left: 97px;
  background-color: #00838f;
  border: 2px solid transparent;
  border-color: #00838f;
  border-radius: 50px;
}
.timeend {
  margin-top: 4px;
  height: 4px;
  width: 4px;
  margin-left: 99px;
  background-color: #00838f;
  border-radius: 50px;
}
.timeline {
  /*border-left: 2px solid #00838f;
  margin-left: 40px;*/
  height: 20px;
  width:2px;
  background-color: #00838f;
  border-radius: 50px;
  margin-left: 100px;
}
.timeitem:hover .timedot:before {
  border-bottom-color: #00b8d4;
}
.timeitem:hover .timedot:after {
  border-top-color: #00b8d4;
}
.timeitem:hover .timedot-left {
  color: #00b8d4;
}
.timedot {
  margin-left: 1px;
  margin-top: 4px;
  position: relative;
  /*width: 0;
  height: 0;
  border: 6px solid transparent;
  border-bottom-color: #00838f;
  top: -6px;
  left: -5px;*/
}
.timedot:before {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  border: 6px solid transparent;
  border-bottom-color: #00838f;
  top: -6px;
  left: -6px;
}
.timedot:after {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  border: 6px solid transparent;
  border-top-color: #00838f;
  top: 6px;
  left: -6px;
}
.timedot-left {
  height: 20px;
  width: 100px;
  font-size: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #00838f;
}
.timedot-right {
  height: 20px;
  font-size: 16px;
  margin-left: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.Projs {
  min-height: 100vh;
}
.projcat Button:hover {
  color: #0097a7;
  /*background-color: #66bb6a;*/
}


.slide-enter {
  opacity: 0;
  /*transform: translateY(-100%);*/
  position: relative;
}
.slide-enter-active {
  opacity: 1;
  /*transform: translateY(-0%);*/
  transition: all 1s;
}
.slide-exit {
  opacity: 1;
  /*transform: translateY(-0%);*/
  position: relative;
}
.slide-exit-active {
  opacity: 0;
  /*transform: translateY(-100%);*/
  transition: all 1s;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
